$main-menu-bg: url('./img/bg/Main-Menu.png');
@font-face {
  font-family: 'Prompt-Reg';
  src: url('./Fonts/Prompt-Regular_0.ttf');
}
@font-face {
  font-family: 'Prompt-Bold';
  src: url('./Fonts/Prompt-Bold_0.ttf');
}

@media only screen and (max-width: 768px) {
  body {
    font-family: 'Prompt-Bold';
    overflow: hidden;
    margin: 0;
  
    .container {
      position: absolute;
      display: flex;
      width: 100%;
      height: 100%;

      background-image: $main-menu-bg;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: center;
  
      .start-menu{
        .visa-logo{
          margin-top: 5rem;
          margin-left: auto;
          margin-right: auto;
          max-width: 68%;
          img{
            width: 100%;
          }
        }

        .game-logo{
          margin-top: 2rem;
          margin-left: auto;
          margin-right: auto;
          max-width: 90%;
          img{
            width: 100%;
          }
        }

        .play-btn{
          width: fit-content;
          margin-top: 3rem;
          margin-left: auto;
          margin-right: auto;

          button{
            font-family: 'Prompt-Bold';
            background-color: rgb(209, 32, 32);
            color: #ffffff;
            border-radius: 30px;
            font-size: 1.8rem;
            padding: 0.6rem 2.2rem 0.6rem 2.2rem;
          }
        }

        .reward-btn{
          width: fit-content;
          margin-top: 1.3rem;
          margin-left: auto;
          margin-right: auto;

          button{
            font-family: 'Prompt-Bold';
            background-color: rgb(209, 32, 32);
            color: #ffffff;
            border-radius: 30px;
            font-size: 1.8rem;
            padding: 0.6rem 3rem 0.6rem 3rem;
          }
        }

        .lower-banner{
          margin-top: 2rem;
          margin-left: auto;
          margin-right: auto;
          max-width: 90%;
          img{
            width: 100%;
          }
        }

      }

      .loading-menu{
        position: absolute;
        left: 5%;
        top: 46%;
        margin: 0 auto;
        max-width: 90%;
        img{
          width: 100%;
        }
      }

    }
  }
}




